import React from "react" 
import { StaticImage } from "gatsby-plugin-image"

export default function ChannelView (){
    
  return (
    <div>
        <h3>Visítanos</h3>
        <ul className="list-inline">
            <li className="list-inline-item">
            <StaticImage src="../images/youtube.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed"  width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/reddit.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/twitter.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/twitch.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed"  width={60}    height={60}/>
            </li>
        </ul>
    </div>
  )
}