import React from "react" 
import { StaticImage } from "gatsby-plugin-image"

export default function CompanyLogos (){
    
  return (
    <div>
        <h3>Métodos de Pago</h3>
        <ul className="list-inline">
            <li className="list-inline-item">
            <StaticImage src="../images/tunki.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/plin.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/yape.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed"  width={60}    height={60}/>
            </li> 
        </ul>
    </div>
  )
}