import React from "react" 
import { StaticImage } from "gatsby-plugin-image"

export default function ChannelAtention (){
    
  return (
    <div>
        <h3>Canales de atención</h3>
        <ul className="list-inline">
            <li className="list-inline-item">
            <StaticImage src="../images/messenger.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed"  width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/telegram.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/whatsapp.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed" width={60}    height={60}/>
            </li>
            <li className="list-inline-item">
            <StaticImage src="../images/instagram.png" alt="A dinosaur"  placeholder="blurred"  layout="fixed"  width={60}    height={60}/>
            </li>
        </ul>
    </div>
  )
}