import React from "react"  
import { Navbar,Nav ,Container} from 'react-bootstrap'; 
import { Link } from "gatsby"
import Toggle from "./helpers/ThemeToggler"
import sun from '../assets/sun.png';
import moon from '../assets/moon.png';
class MenuNav extends React.Component {
  state = {
    theme: null,
    activeClass: '',
  };
  componentDidMount() {
    this.setState({ theme: window.__theme });
    window.__onThemeChange = () => {
      this.setState({ theme: window.__theme });
    };
    window.addEventListener('scroll', () => {
      let activeClass = '';
       if(window.scrollY >= 60){
          activeClass = 'active';
      }
      this.setState({ activeClass });
   });
  }
  render() {
    const { siteTitle,menuLinks} = this.props;

    return (
      <Navbar  className={`top-bar ${this.state.activeClass}`} collapseOnSelect fixed="top" expand="sm"> 
      <Container>
      <Link to="/" className="navbar-brand">{siteTitle}</Link> 
      {this.state.theme !== null ? (
            <Toggle className="me-3"
              icons={{
                checked: (
                  <img
                    src={moon}
                    width="16"
                    height="16"
                    role="presentation"
                    alt=""
                    style={{ pointerEvents: 'none' }}
                  />
                ),
                unchecked: (
                  <img
                    src={sun}
                    width="16"
                    height="16"
                    alt=""
                    role="presentation"
                    style={{ pointerEvents: 'none' }}
                  />
                ),
              }}
              checked={this.state.theme === 'dark'}
              onChange={e =>
                window.__setPreferredTheme(
                  e.target.checked ? 'dark' : 'light'
                )
              }
            />
          ) : (
            <div style={{ height: '24px' }} />
          )}
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="me-auto">
        {menuLinks.map(link => (
                  <Nav.Item  key={link.name}>
                    <Link to={link.link} className="nav-link">{link.name}</Link> 
                  </Nav.Item>
                ))} 
             
        </Nav> 
      </Navbar.Collapse> 
      </Container>
    </Navbar>
    );
  }
}

export default MenuNav;  